import { useState } from 'react';

export default function useStep(stepCount) {
  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((activeStep + 1) % stepCount);
  };

  const handlePrevious = () => {
    setActiveStep((activeStep - 1 + stepCount) % stepCount);
  };

  const isLast = activeStep === stepCount - 1;
  const isFirst = activeStep === 0;

  return [activeStep, handleNext, handlePrevious, isLast, isFirst];
}
