import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import TextField from '@mui/material/TextField';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';

import * as yup from 'yup';

import FormStepperNavigation from 'components/FormStepperNavigation';
import BoxForm from 'components/BoxForm';
import utils from 'utils';

export default function TitleDateForm({
  title,
  date,
  handleTitleChange,
  handleDateChange,
  handleNext,
  handlePrevious,
  isLast,
  isFirst,
  activeStep,
}) {
  const [titleError, setTitleError] = useState('');
  const [dateError, setDateError] = useState('');

  const [titleTouched, setTitleTouched] = useState(false);
  const [dateTouched, setDateTouched] = useState(false);

  const titleSchema = yup.object({
    title: yup.string().required('Memory Title is required').max(40, 'Maximum length of memory title is 40'),
  }).required();

  const dateSchema = yup.object({
    date: yup.date('Enter date by clicking calendar icon on the right end of date picker')
      .required('Memory Date is required').nullable(),
  }).required();

  const isValid = async () => {
    const titleValid = await utils.isFieldValid('title', title, setTitleError, titleSchema);
    const dateValid = await utils.isFieldValid('date', date, setDateError, dateSchema);
    return titleValid && dateValid;
  };

  useEffect(() => {
    if (titleTouched) {
      utils.isFieldValid('title', title, setTitleError, titleSchema);
    }
    if (dateTouched) {
      utils.isFieldValid('date', date, setDateError, dateSchema);
    }
  }, [title, date]);

  const submitForm = async (handleCallback) => {
    const valid = await isValid();
    if (valid) {
      handleCallback();
    }
  };

  const onTitleChange = (event) => {
    setTitleTouched(true);
    handleTitleChange(event);
  };

  const onDateChange = (event) => {
    setDateTouched(true);
    handleDateChange(event);
  };

  return (
    <BoxForm>
      <TextField
        required
        onChange={onTitleChange}
        id="outlined-required"
        label="Memory Name"
        defaultValue={title}
        helperText={titleError || "E.g. Gogol's 2nd Birthday"}
        error={!!titleError}
      />
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <DatePicker
          label="Memory Date"
          value={date}
          onChange={onDateChange}
          placeholder="dd/MM/yyyy"
          inputFormat="dd/MM/yyyy"
          maxDate={new Date()}
          renderInput={(props) => (
            <TextField
              readOnly
              required
          // eslint-disable-next-line react/jsx-props-no-spreading
              {...props}
              error={!!dateError}
              helperText={dateError || ''}
            />
          )}
        />
      </LocalizationProvider>
      <FormStepperNavigation
        activeStep={activeStep}
        handleNext={() => submitForm(handleNext)}
        handlePrevious={handlePrevious}
        isLast={isLast}
        isFirst={isFirst}
      />
    </BoxForm>
  );
}

TitleDateForm.defaultProps = {
  date: '',
};

TitleDateForm.propTypes = {
  activeStep: PropTypes.number.isRequired,
  date: PropTypes.string,
  isLast: PropTypes.bool.isRequired,
  isFirst: PropTypes.bool.isRequired,
  title: PropTypes.string.isRequired,

  handleDateChange: PropTypes.func.isRequired,
  handleNext: PropTypes.func.isRequired,
  handlePrevious: PropTypes.func.isRequired,
  handleTitleChange: PropTypes.func.isRequired,
};
