import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Typography from '@mui/material/Typography';

export default function ToggleViewMode({ mode, handleModeChange, modes }) {
  return (
    <Divider>
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 2 }}>
        <ToggleButtonGroup
          exclusive
          value={mode}
          aria-label="view-change"
          color="primary"
          sx={{ bgcolor: 'button.main' }}

        >
          {modes.map((modeName) => (
            <ToggleButton
              key={modeName}
              value={modeName}
              onClick={() => handleModeChange(`${modeName}`)}
              sx={{ border: 2, borderColor: 'primary.main' }}
            >
              <Typography variant="h6">{modeName}</Typography>
            </ToggleButton>
          ))}
        </ToggleButtonGroup>
      </Box>
    </Divider>
  );
}

ToggleViewMode.propTypes = {
  mode: PropTypes.string.isRequired,
  handleModeChange: PropTypes.func.isRequired,
  modes: PropTypes.PropTypes.arrayOf(PropTypes.string).isRequired,
};
