import PropTypes from 'prop-types';
import { useState } from 'react';
import Box from '@mui/material/Box';

import FormStepperNavigation from 'components/FormStepperNavigation';
import ErrorText from 'components/ErrorText';
import StoryEditor from './StoryEditor';

export default function StoryForm({
  story,
  handleStoryChange,
  handleNext,
  handlePrevious,
  isLast,
  isFirst,
  progressDialog,
}) {
  const maxStoryLength = 100000;
  const [storyError, setStoryError] = useState('');

  const submitForm = (handleCallback) => {
    const memoryText = story;
    if (memoryText.length <= maxStoryLength) {
      setStoryError('');
      handleCallback();
    } else {
      setStoryError(`Maximum story length is: ${maxStoryLength}`);
    }
  };

  return (
    <>
      <StoryEditor
        storyError={storyError}
        story={story}
        handleStoryChange={handleStoryChange}
      />
      <Box sx={{ p: 1, m: 1 }}>
        <ErrorText text={storyError} />
      </Box>
      <FormStepperNavigation
        handleNext={() => submitForm(handleNext)}
        handlePrevious={handlePrevious}
        isLast={isLast}
        isFirst={isFirst}
      />
      {progressDialog}
    </>
  );
}

StoryForm.propTypes = {
  story: PropTypes.string.isRequired,
  handleStoryChange: PropTypes.func.isRequired,
  handleNext: PropTypes.func.isRequired,
  handlePrevious: PropTypes.func.isRequired,
  isLast: PropTypes.bool.isRequired,
  isFirst: PropTypes.bool.isRequired,
  progressDialog: PropTypes.node.isRequired,
};
