import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import PhotoAlbum from 'react-photo-album';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
//  import { alpha } from '@mui/material/styles';
import ThumbnailChooser from 'components/ThumbnailChooser';
/* import IconButton from '@mui/material/IconButton'; */
/* import CloseIcon from '@mui/icons-material/Close'; */
import Button from '@mui/material/Button';

import ImageOptionsDialog from 'components/ImageOptionsDialog';
import AddCaptionButton from 'components/AddCaptionButton';
import RemoveSelectedImageButton from 'components/RemoveSelectedImageButton';
import utils from 'utils';

export default function ImagePreviewer({
  imageDetails,
  handleRemoveImage,
  thumbnail,
  handleThumbnailChange,
  handleCaptionChange,
  //  setDimensions,
  //  resizeHeights,
}) {
  console.log(imageDetails);
  const [images, setImages] = useState([]);
  const [editingIndex, setEditingIndex] = useState(null);
  const [open, setOpen] = useState(false);
  const [arrangement, setArrangement] = useState(null);

  const photos = () => imageDetails.map((photo, index) => ({
    alt: index,
    src: photo.url,
    width: photo.width,
    height: photo.height,
    title: photo.caption || '',
  }));

  useEffect(() => setImages(photos), [imageDetails]);
  useEffect(() => utils.handleArrangement(imageDetails, setArrangement), [imageDetails]);
  console.log(images);

  const handleClose = () => {
    setEditingIndex(null);
    setOpen(false);
  };

  const handleCaptionClick = (index) => {
    setEditingIndex(index);
    setOpen(true);
  };

  const renderPhotos = ({
    layout,
    imageProps: {
      alt, style, title, ...restImageProps
    },
  }) => {
    const imgIndex = Number(alt);
    return (
      <Button component="div" sx={{ padding: '2px' }}>
        <img
          alt={alt}
          // eslint-disable-next-line react/jsx-props-no-spreading
          style={{ ...style, width: layout.width, padding: 0 }}
          // eslint-disable-next-line react/jsx-props-no-spreading
          {...restImageProps}
        />
        <RemoveSelectedImageButton index={imgIndex} handleRemoveImage={handleRemoveImage} />
        <ThumbnailChooser
          index={imgIndex}
          thumbnail={thumbnail}
          handleThumbnailChange={handleThumbnailChange}
        />
        <AddCaptionButton handleClick={() => handleCaptionClick(imgIndex)} caption={title} />
      </Button>
    );
  };

  let editingSrc = '';
  let editingCaption = '';
  if (editingIndex !== null) {
    editingSrc = imageDetails[editingIndex].url;
    if ('caption' in imageDetails[editingIndex]) {
      editingCaption = imageDetails[editingIndex].caption;
    }
  }
  console.log(editingIndex);
  return images.length ? (
    <Box component="div" sx={{ py: 2 }}>
      <Divider />
      <Typography sx={{ p: 2 }} variant="h6" align="center">
        Selected Images
      </Typography>
      <PhotoAlbum
        layout={arrangement}
        photos={images}
        spacing={1}
        renderPhoto={renderPhotos}
        rowConstraints={utils.handleMaxPhotos}
        columns={utils.handleColumns}
      />
      <ImageOptionsDialog
        imageCaption={editingCaption}
        index={editingIndex}
        src={editingSrc}
        open={open}
        handleClose={handleClose}
        handleSubmit={handleCaptionChange}
      />
    </Box>
  ) : null;
}

ImagePreviewer.propTypes = {
  imageDetails: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    url: PropTypes.string,
    size: PropTypes.number,
    width: PropTypes.number,
    height: PropTypes.number,
    caption: PropTypes.string,
  })).isRequired,
  handleRemoveImage: PropTypes.func.isRequired,
  thumbnail: PropTypes.number.isRequired,
  handleThumbnailChange: PropTypes.func.isRequired,
  handleCaptionChange: PropTypes.func.isRequired,
  //  setDimensions: PropTypes.func.isRequired,
  //  resizeHeights: PropTypes.arrayOf(PropTypes.number).isRequired,
};
