import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import TextareaAutosize from '@mui/material/TextareaAutosize';
import { useRef, useEffect } from 'react';

export default function StoryEditor({
  storyError, story, handleStoryChange,
}) {
  const storyRef = useRef();

  useEffect(() => {
    storyRef.current.focus();
  }, []);

  return (
    <Box
      sx={{
        p: 2,
        m: 1,
        my: 2,
        border: 1,
        borderColor: storyError ? 'error.main' : 'primary.main',
        bgcolor: 'input.background',
        minHeight: '200px',
        borderRadius: 5,
      }}
      onClick={() => storyRef.current.focus()}
    >
      <TextareaAutosize
        autoFocus
        aria-label="minimum height"
        placeholder="Tell your story..."
        value={story}
        onChange={handleStoryChange}
        style={{
          width: '100%',
          minHeight: '184px',
          maxHeight: '70vh',
          boxSizing: 'border-box',
          paddingTop: '5px',
          border: 0,
          overflow: 'auto',
          outline: 'none',
          resize: 'vertical',
          borderRadius: 5,
          '&:focus': {
            border: 0,
          },
        }}
        ref={storyRef}
      />
    </Box>
  );
}

StoryEditor.propTypes = {
  storyError: PropTypes.string.isRequired,
  story: PropTypes.string.isRequired,
  handleStoryChange: PropTypes.func.isRequired,
};
