import PropTypes from 'prop-types';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import Typography from '@mui/material/Typography';
import Chip from '@mui/material/Chip';
import MemoryThumbnailCard from './MemoryThumbnailCard';

function ThumbnailImage({
  imageSrc, imageSrcSet, title, handleImageLoad,
}) {
  return (
    <CardMedia
      component="img"
      src={imageSrc}
      srcSet={imageSrcSet}
      alt={title}
      sx={{
        textAlign: 'center',
        objectFit: 'cover',
        height: {
          lg: 350,
          md: 300,
          xs: 250,
        },
      }}
      onLoad={handleImageLoad}
    />
  );
}

function ThumbnailDetails({
  title, date, avatar, shared,
}) {
  return (
    <CardContent>
      <CardHeader
        disableTypography
        avatar={avatar}
        title={(
          <Typography
            noWrap
            sx={{
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              width: { xs: 'calc(75%)', sm: 'calc(100%)' },
              fontWeight: 600,
            }}
          >
            {title}
            {
              shared !== '' ? (
                <>
                  {' '}
                  <Chip
                    label={shared}
                    size="small"
                    variant="filled"
                    color={(shared === 'Shared') ? 'success' : 'default'}
                    sx={{ height: 'auto' }}
                  />
                </>
              ) : null
            }

          </Typography>
        )}
        subheader={<Typography sx={{ color: 'muted.main' }}>{date}</Typography>}
        sx={{
          padding: 0,
          color: 'faded.black',
        }}
      />
    </CardContent>
  );
}

export default function MemoryThumbnail({
  title,
  date,
  avatar,
  shared,
  thumbnailImage,
  thumbnailImageSrcSet,
  handleClick,
  handleImageLoad,
}) {
  return (
    <MemoryThumbnailCard handleClick={handleClick}>
      <ThumbnailImage
        imageSrc={thumbnailImage}
        imageSrcSet={thumbnailImageSrcSet}
        title={title}
        handleImageLoad={handleImageLoad}
      />
      <ThumbnailDetails title={title} date={date} avatar={avatar} shared={shared} />
    </MemoryThumbnailCard>
  );
}

ThumbnailDetails.defaultProps = {
  title: 'Memory Thumbnail Image',
  date: 'Memory Date',
  avatar: '',
  shared: '',
};

ThumbnailDetails.propTypes = {
  title: PropTypes.string,
  date: PropTypes.string,
  avatar: PropTypes.node,
  shared: PropTypes.string,
};

ThumbnailImage.defaultProps = {
  title: 'Memory Thumbnail Image',
  imageSrc: '',
  imageSrcSet: '',
  handleImageLoad: () => {},
};

ThumbnailImage.propTypes = {
  title: PropTypes.string,
  imageSrc: PropTypes.string,
  imageSrcSet: PropTypes.string,
  handleImageLoad: PropTypes.func,
};

MemoryThumbnail.defaultProps = {
  thumbnailImageSrcSet: '',
  handleImageLoad: () => {},
  avatar: '',
  shared: '',
};

MemoryThumbnail.propTypes = {
  title: PropTypes.oneOfType([PropTypes.element, PropTypes.string]).isRequired,
  date: PropTypes.oneOfType([PropTypes.element, PropTypes.string]).isRequired,
  avatar: PropTypes.node,
  shared: PropTypes.string,
  thumbnailImage: PropTypes.string.isRequired,
  handleClick: PropTypes.func.isRequired,
  thumbnailImageSrcSet: PropTypes.string,
  handleImageLoad: PropTypes.func,
};
