import api from '../Api';

function UploadImages(
  bucket,
  authURL,
  memoryKey,
  title,
  imageDetails,
  imageFiles,
  incrementImageCount,
) {
  const paramList = imageDetails.map((detail, index) => {
    const imgFile = imageFiles[index];
    return {
      Key: ['memories', memoryKey, 'images', detail.name].join('/'),
      Body: imgFile,
      ContentType: imgFile.type,
    };
  });
  const uploadPromises = api.uploadMultipleImages(bucket, authURL, paramList, incrementImageCount);
  return Promise.allSettled(uploadPromises);
}

async function ValidateDetails(bucket, authURL, memoryDetails) {
  const detailsJSON = JSON.stringify(memoryDetails);
  const validateResp = await api.validateDetails(bucket, authURL, detailsJSON);
  if (validateResp.status === 'error') {
    return false;
  }
  return validateResp.Valid === true;
}

async function UploadDetails(bucket, authURL, memoryDetails) {
  const detailsJSON = JSON.stringify(memoryDetails);
  const memoryDetailsKey = ['memories', memoryDetails.key, 'details.json'].join('/');
  const uploadResp = await api.upload(bucket, authURL, memoryDetailsKey, detailsJSON);
  return (uploadResp && uploadResp.ok === true);
}

const InitializeMemory = async (
  bucket,
  authURL,
  memoryDetails,
  handleProgress,
  updateMessageStatus,
  addMessage,
) => {
  handleProgress(0);
  addMessage('Initializing memory', false);
  const valid = await ValidateDetails(bucket, authURL, memoryDetails);
  let initRes;
  if (valid === true) {
    initRes = await UploadDetails(bucket, authURL, memoryDetails);
    updateMessageStatus('Initializing Memory', initRes);
    handleProgress(10);
  } else {
    updateMessageStatus('Initializing Memory', 'failed');
  }
  return valid;
};

const HandleImages = async (
  bucket,
  authURL,
  memoryKey,
  title,
  imageDetails,
  imageFiles,
  incrementImageCount,
  handleProgress,
) => {
  const imagesResults = await UploadImages(
    bucket,
    authURL,
    memoryKey,
    title,
    imageDetails,
    imageFiles,
    incrementImageCount,
  );
  for (let i = 0; i < imagesResults.length; i += 1) {
    if ((imagesResults[i].status === 'rejected') || (imagesResults[i].value.ok !== true)) {
      return false;
    }
  }
  handleProgress(95);
  return true;
};

const CreateMemory = async (
  bucket,
  authURL,
  memoryDetails,
  handleProgress,
  updateMessageStatus,
  addMessage,
  imageFiles,
  incrementImageCount,
) => {
  // Validate details and upload if valid
  const initMemResults = await InitializeMemory(
    bucket,
    authURL,
    memoryDetails,
    handleProgress,
    updateMessageStatus,
    addMessage,
  );

  let imagesResults;
  if (initMemResults === true) {
    // Upload original images
    imagesResults = await HandleImages(
      bucket,
      authURL,
      memoryDetails.key,
      memoryDetails.title,
      memoryDetails.imageDetails,
      imageFiles,
      incrementImageCount,
      handleProgress,
    );

    if (imagesResults === true) {
      const finalizeResults = await api.finalizeMemory(bucket, authURL, memoryDetails.key);
      if (parseInt(finalizeResults.MemoryID, 10) !== memoryDetails.key) {
        return false;
      }
      return true;
    }
    updateMessageStatus('Uploading Original Images', 'failed');
  }

  return false;
};

export default { CreateMemory };
