import { useState } from 'react';

export default function useProgressMessage() {
  const [messages, setMessages] = useState([]);

  const addMessage = (message, done) => {
    const newMessage = [{ message, done }];
    setMessages((arr) => arr.concat(newMessage));
  };

  const updateMessageStatus = (message, done) => {
    const newMessage = [{ message, done }];
    setMessages((arr) => arr.slice(0, -1).concat(newMessage));
  };

  return [messages, addMessage, updateMessageStatus];
}
