import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import NavigationButton from 'components/NavigationButton';

export default function FormStepperNavigation({
  handleNext, handlePrevious, isLast, isFirst,
}) {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'row',
        pt: 2,
        width: 1,
        justifyContent: 'space-evenly',
      }}
    >
      { isFirst ? (
        <NavigationButton disabled handleClick={() => {}} label="Back" />
      ) : (
        <NavigationButton handleClick={handlePrevious} label="Back" />
      )}
      { isLast ? (
        <NavigationButton handleClick={handleNext} label="Create Memory" />
      ) : (
        <NavigationButton handleClick={handleNext} label="Next" />
      )}
    </Box>
  );
}

FormStepperNavigation.propTypes = {
  handleNext: PropTypes.func.isRequired,
  handlePrevious: PropTypes.func.isRequired,
  isLast: PropTypes.bool.isRequired,
  isFirst: PropTypes.bool.isRequired,
};
