import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Dialog from '@mui/material/Dialog';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import Divider from '@mui/material/Divider';

import FormStepperNavigation from 'components/FormStepperNavigation';
import BoxForm from 'components/BoxForm';
import ErrorText from 'components/ErrorText';
import ImagePreviewer from './ImagePreviewer';
import ImageSelector from './ImageSelector';

export default function ImageForm({
  imageDetails,
  thumbnail,
  handleSelectedImageChange,
  handleThumbnailChange,
  removeSelectedImage,
  handleNext,
  handlePrevious,
  isLast,
  isFirst,
  handleCaptionChange,
  //  setDimensions,
  //  resizeHeights,
}) {
  const maxAllowedImages = 15;
  const minAllowedImages = 1;
  const [imageError, setImageError] = useState('');
  const [touched, setTouched] = useState(false);
  const [totalImages, setTotalImages] = useState(0);
  const [open, setOpen] = useState(false);

  function isValid() {
    let valid = true;
    setImageError('');
    if (imageDetails.length < minAllowedImages) {
      setImageError('Need atleast one image to create a memory');
      valid = false;
    }

    if (imageDetails.length > maxAllowedImages) {
      setImageError(`Maximum Allowed Images per Memory: ${maxAllowedImages}`);
      valid = false;
    }

    return valid;
  }

  useEffect(() => {
    if (touched) isValid();
  }, [imageDetails, touched]);

  useEffect(() => {
    if (totalImages < imageDetails.length) {
      setTotalImages(imageDetails.length);
    }
    if (totalImages > imageDetails.length) {
      setOpen(true);
    }
    if (totalImages === imageDetails.length) {
      setOpen(false);
    }
  }, [imageDetails, totalImages]);

  const handleSubmit = (handleCallback) => {
    if (isValid()) {
      handleCallback();
    }
  };

  const handleImageChange = (event) => {
    setTouched(true);
    if (event.target.files) {
      setTotalImages(event.target.files.length + imageDetails.length);
    }
    handleSelectedImageChange(event, isValid);
  };

  const handleImageDelete = (index) => {
    setTotalImages((prevCount) => prevCount - 1);
    removeSelectedImage(index);
  };

  return (
    <BoxForm>
      <Grid item>
        <ImageSelector
          handleChange={handleImageChange}
        />
      </Grid>
      <Grid item container direction="column" alignItems="center">
        <Typography color="gray" sx={{ marginTop: 2 }}>
          Total Images Selected:
          {' '}
          {imageDetails.length}
          /
          {maxAllowedImages}
        </Typography>
        <ErrorText text={imageError} />
      </Grid>
      <FormStepperNavigation
        handleNext={() => handleSubmit(handleNext)}
        handlePrevious={handlePrevious}
        isLast={isLast}
        isFirst={isFirst}
      />
      <Grid item>
        <ImagePreviewer
          imageDetails={imageDetails}
          handleRemoveImage={handleImageDelete}
          thumbnail={thumbnail}
          handleThumbnailChange={handleThumbnailChange}
          handleCaptionChange={handleCaptionChange}
          //  setDimensions={setDimensions}
          //  resizeHeights={resizeHeights}
        />
      </Grid>
      <Dialog
        open={open}
        fullWidth
        sx={{
          p: 1,
        }}
      >
        <Card sx={{ maxWidth: 1, p: 1 }}>
          <CardHeader title="Adding Images" sx={{ textAlign: 'center' }} />
          <Divider />
          <CardContent>
            <Typography variant="h6" sx={{ color: 'muted.main' }}>
              Total Images Selected:
              {imageDetails.length}
              /
              {maxAllowedImages}
            </Typography>
            <Stack justifyContent="center" sx={{ py: 2 }}>
              <Typography variant="h5" color="error">{imageError}</Typography>
            </Stack>
          </CardContent>
        </Card>
      </Dialog>
    </BoxForm>
  );
}

ImageForm.propTypes = {
  imageDetails: PropTypes.arrayOf(PropTypes.shape({
    url: PropTypes.string,
    name: PropTypes.string,
    size: PropTypes.number,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
  handleSelectedImageChange: PropTypes.func.isRequired,
  removeSelectedImage: PropTypes.func.isRequired,
  thumbnail: PropTypes.number.isRequired,
  handleThumbnailChange: PropTypes.func.isRequired,
  handleNext: PropTypes.func.isRequired,
  handlePrevious: PropTypes.func.isRequired,
  isLast: PropTypes.bool.isRequired,
  isFirst: PropTypes.bool.isRequired,
  handleCaptionChange: PropTypes.func.isRequired,
  //  setDimensions: PropTypes.func.isRequired,
  //  resizeHeights: PropTypes.arrayOf(PropTypes.number).isRequired,
};
